@import url(https://fonts.googleapis.com/css?family=Fondamento);
.nopadding {
  padding: 0; }

.black-font p {
  color: black; }

.tile-column {
  padding-left: 0;
  padding-right: 0;
  right: 0.5em; }

.tile {
  padding-top: 3px; }

.crystal {
  display: inline-block; }
  .crystal h3 {
    font-weight: 800;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -55%);
            transform: translate(-50%, -55%);
    text-shadow: -1px -1px 0 #4c494a, 1px -1px 0 #4c494a, -1px 1px 0 #4c494a, 1px 1px 0 #4c494a; }

.crystal-container {
  -webkit-transform: translate(-10%, 15%);
          transform: translate(-10%, 15%); }

.tile-container {
  margin-top: 5px;
  margin-right: 1px;
  margin-left: 1px; }

.card-number {
  -webkit-transform: translate(-10%, 25%);
          transform: translate(-10%, 25%); }

.tile-container {
  background-image: url(/React-Hearthstone-Deck-Builder/static/media/wood.35203464.jpg);
  background-size: cover; }

.hero-img--circle-border {
  width: 50px;
  height: 73px;
  position: relative;
  overflow: hidden;
  border: 10px solid transparent;
  /* Safari 3.1-5 */
  /* Opera 11-12.1 */
  border-image: url(/React-Hearthstone-Deck-Builder/static/media/wood.35203464.jpg) 30 round;
  -webkit-transform: translate(10%, 20%);
          transform: translate(10%, 20%); }
  .hero-img--circle-border img {
    display: inline;
    margin: -15px 0px 0px -22px;
    height: 190%;
    width: auto; }

.card {
  background: 'none';
  border: 'none'; }
  .card.card--disabled img {
    opacity: 0.5; }

.tile-button {
  border: none;
  background: transparent;
  position: relative; }
  .tile-button:hover + .tile-preview-image {
    display: block; }

.cards-container--preview .preview-background {
  position: fixed;
  background: black;
  opacity: 0.5;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 99; }

.deck-container {
  z-index: 999; }

.tile-preview-image {
  display: none;
  position: absolute;
  -webkit-transform: translate(-97.5%, -45%);
          transform: translate(-97.5%, -45%); }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  background-image: url(/React-Hearthstone-Deck-Builder/static/media/dark-honeycomb.c16de435.png);
  background-repeat: repeat; }

p {
  color: white; }

